.Menu
{
    float: right;
    background-color: #EDEEF2;
    height: 100vh;
    width: 30%;

    .nav
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        margin: 0;
        list-style: none;
        padding: 0;
        
        li
        {
            display: flex;
            align-items: center;
            height: 25%;
            font-family: 'Montserrat';
            font-weight: bolder;
            text-transform: uppercase;
            cursor: pointer;
            padding-left: 15px;
            
            &:hover
            {
                color: white;
                &:nth-child(1)
                {
                    background-color: #2490f4;
                }

                &:nth-child(2)
                {
                    background-color: #5c1f84;
                }

                &:nth-child(3)
                {
                    background-color: #96cc00;
                }

                &:nth-child(4)
                {
                    background-color: #b3001b;
                }
            }

            img
            {                
                width: 100px;
            }

            a
            {
                margin: auto;
            }
        }
    }
}